<script>
import json from "../json/pitanja.json";

export default {
  data() {
    return {
      allQuestions: json,
      questions: [],
      model: [],
      showResults: false,
      disabled: false,
      correctAnswers: 0,
      percentage: 0,
      shuffle: true,
      numberOfQuestions: 40,
      selectedItem: 0,
      items: [
        {
          text: 'Za pokretanje simulacije testa pritisnite "Simulacija ispita"dugme koje se nalazi ispod ovog teksta.',
          icon: "mdi-clock",
        },
        {
          text: 'Kada odgovorite na sva pitanja, klikom na dugme "Proveri" otvorice se mali blok u kome se ispisuje vas rezultat (broj tacnih odgovora i procenat)',
          icon: "mdi-account",
        },
        {
          text: 'Ako zelite da ponovo pokrenete simulaciju pritisnite dugme "PROBAJPONOVO"',
          icon: "mdi-flag",
        },
      ],
    };
  },
  created() {
    //this.randomizeQestions();
  },
  mounted() {},
  methods: {
    randomizeQestions() {
      this.disabled = false;
      this.showResults = false;

      if (this.shuffle) {
        let shuffled = this.shuffleVueArray(this.allQuestions);
        this.questions = shuffled.splice(0, this.numberOfQuestions);
        this.allQuestions = shuffled;
      } else {
        this.questions = this.allQuestions.splice(0, this.numberOfQuestions);
      }

      this.init();
    },
    init() {
      for (let i = 0; i < this.questions.length; i++) {
        const item = this.questions[i];
        // Remove question number
        this.questions[i].pitanje = item.pitanje.replace(/^(\d*\.)/gm, " ");
        // Shuffle multiple data
        if (this.questions[i].multiple.length) {
          this.questions[i].multiple = this.shuffleVueArray(
            this.questions[i].multiple
          );
          this.removeThingsFromMultipleOptions(i);
        } else {
          // Shuffle answers data
          this.questions[i].odgovori = this.shuffleVueArray(
            this.questions[i].odgovori
          );
          this.removeThingsFromQuestionOptions(i);
        }
        // Init model which will be used to store user response
        this.model[i] = [];
        if (item.multiple.length > 0) {
          this.model[i]["answer"] = null;
        } else {
          this.model[i]["option"] = null;
        }
      }
    },
    removeThingsFromMultipleOptions(index) {
      for (let i = 0; i < this.questions[index]["multiple"].length; i++) {
        this.questions[index]["multiple"][i].option =
          this.questions[index]["multiple"][i].option.substring(2);
      }
    },
    removeThingsFromQuestionOptions(index) {
      for (let i = 0; i < this.questions[index]["odgovori"].length; i++) {
        this.questions[index]["odgovori"][i].answer =
          this.questions[index]["odgovori"][i].answer.substring(2);
      }
    },
    validateAnswers() {
      // Calculate total correct answers
      let type = "";
      let correctAnswers = 0;
      let answerIndex = -1;
      for (let i = 0; i < this.questions.length; i++) {
        const item = this.questions[i];
        type = item.multiple.length ? "option" : "answer";
        answerIndex = this.model[i][type];
        if (item.multiple.length) {
          if (typeof item.multiple[answerIndex] === "undefined") {
            alert("Molimo odgovorite na sva pitanja!");
            return;
          }
          correctAnswers += item.multiple[answerIndex].is_true ? 1 : 0;
        } else {
          if (typeof item.odgovori[answerIndex] === "undefined") {
            alert("Molimo odgovorite na sva pitanja!");
            return;
          }
          correctAnswers += item.odgovori[answerIndex].is_correct ? 1 : 0;
        }
      }
      // Disable inupts and display the results
      this.disabled = true;
      this.showResults = true;

      this.correctAnswers = correctAnswers;
      this.percentage = (correctAnswers * 100) / this.questions.length;
    },
    shuffleVueArray(array) {
      let currentIndex = array.length,
        randomIndex;

      // While there remain elements to shuffle...
      while (currentIndex != 0) {
        // Pick a remaining element...
        randomIndex = Math.floor(Math.random() * currentIndex);
        currentIndex--;

        // And swap it with the current element.
        [array[currentIndex], array[randomIndex]] = [
          array[randomIndex],
          array[currentIndex],
        ];
      }

      return array;
    },
  },
};
</script>

<template>
  <v-container class="grey lighten-5">
    <v-row no-gutters v-if="questions.length == 0">
      <v-col cols="12">
        <v-card class="pa-2" outlined tile>
          <h2>PITANJA - Vazduhoplovni propisi</h2>
          <p>
            PRIMER ISPITA ZA STICANjE POTVRDE ZA UPRAVLjANjE BESPILOTNIM
            VAZDUHOPLOVIMA
          </p>
          <p class="font-weight-medium">
            Broj izdanja: 02. Datum izdanja: 01.03.2020. Odeljenje
            Vazduhoplovnog Osoblja.
          </p>
          <p class="font-weight-black">
            Ova stranica nije oficijalna, tacnije nema nikakve veze sa
            DIREKTORATOM CIVILNOG VAZDUHOPLOVSTVA REPUBLIKE SRBIJE.
          </p>
          <p class="font-italic">
            Spremam se za polaganje ispita i hteo sam da simuliram ispit, rekoh
            zasto ne podeliti i sa ostalima :).
          </p>
          <p>
            Pitanja su preuzeta sa
            <a
              href="http://cad.gov.rs/lat/strana/23351/dokumentacija-za-proveru-znanja-operatera-bespilotnih-vazduhoplova"
              >cad.gov.rs</a
            >
            sajta (Direktorat civilnog vazduhoplovstva Republike Srbije).
          </p>
          <p>Ver: 20221803.13.23</p>
          <v-list dense>
            <v-subheader>Upustvo za upotrebu:</v-subheader>
            <v-list-item-group v-model="selectedItem" color="primary">
              <v-list-item v-for="(item, i) in items" :key="i">
                <v-list-item-icon>
                  <v-icon v-text="item.icon"></v-icon>
                </v-list-item-icon>
                <v-list-item-content>
                  <v-list-item-title v-text="item.text"></v-list-item-title>
                </v-list-item-content>
              </v-list-item>
            </v-list-item-group>
          </v-list>
          <br />
          <v-btn color="primary" elevation="1" @click="randomizeQestions"
            >Simulacija ispita (40 nasumicnih pitanja)</v-btn
          >
        </v-card>
      </v-col>
    </v-row>

    <v-row no-gutters v-if="questions.length > 0">
      <v-col cols="12">
        <v-card class="pa-2" outlined tile>
          <div
            class="question"
            v-for="(item, index) in questions"
            :key="item.pitanje"
          >
            <p>{{ index + 1 }}. {{ item.pitanje }}</p>
            <ul class="question-list">
              <li
                v-for="(answer, answerIndex) in item.odgovori"
                :key="answer.answer"
                v-bind:class="{
                  correct:
                    showResults &&
                    model[index]['answer'] == answerIndex &&
                    answer.is_correct,
                  incorrect:
                    showResults &&
                    model[index]['answer'] == answerIndex &&
                    !answer.is_correct,
                  'default-correct': showResults && answer.is_correct,
                }"
              >
                <input
                  v-if="item.multiple.length === 0"
                  type="radio"
                  v-model="model[index]['answer']"
                  :value="answerIndex"
                  :id="index + '_answer_' + answerIndex"
                  :name="'question_' + index + '_answer'"
                  :disabled="disabled"
                />
                {{ answer.answer }}
              </li>
            </ul>
            <div v-if="item.multiple.length > 0">
              <p>
                Šta od gore navedenog predstavlja kombinaciju tačnih odgovora:
              </p>
              <ul class="question-list">
                <li
                  v-for="(option, optionIndex) in item.multiple"
                  :key="optionIndex"
                  v-bind:class="{
                    correct:
                      showResults &&
                      model[index]['option'] == optionIndex &&
                      option.is_true,
                    incorrect:
                      showResults &&
                      model[index]['option'] == optionIndex &&
                      !option.is_true,
                    'default-correct': showResults && option.is_true,
                  }"
                >
                  <div>
                    <input
                      type="radio"
                      v-model="model[index]['option']"
                      :value="optionIndex"
                      :id="index + '_option_' + optionIndex"
                      :name="'question_' + index + '_option'"
                      :disabled="disabled"
                    />
                    {{ option.option }}
                  </div>
                </li>
              </ul>
            </div>
            <v-divider></v-divider>
          </div>

          <v-btn
            v-if="!showResults && questions.length > 0"
            color="primary"
            elevation="1"
            @click="validateAnswers"
            >Proveri</v-btn
          >
        </v-card>
      </v-col>
    </v-row>

    <v-card
      v-if="showResults"
      color="#385F73"
      dark
      elevation="2"
      class="ml-2 mt-3"
      fab
      icon
    >
      <v-card-title class="text-h5"> Rezultat </v-card-title>
      <v-card-subtitle
        >Broj tacnih odgovora je {{ correctAnswers }} ({{
          percentage
        }}%)</v-card-subtitle
      >

      <v-card-actions>
        <v-btn class="ml-2 mt-3" fab icon height="40px" right width="40px">
          <v-icon>mdi-play</v-icon>
        </v-btn>

        <v-btn
          class="ml-2 mt-5"
          @click="randomizeQestions"
          outlined
          rounded
          small
        >
          Probaj ponovo
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-container>
</template>

<style>
ul.question-list {
  list-style-type: none;
}
.question {
  margin-bottom: 10px;
}
li {
  padding: 2px 4px;
}
li.default-correct {
  border-radius: 4px;
  color: black;
  background-color: #4caf4f05;
  border: thin solid silver;
}
li.correct {
  border-radius: 4px;
  color: #4caf50;
  background-color: #e9f5ea;
}
li.incorrect {
  color: #ff5252;
  border-radius: 4px;
  border: thin solid #ff5252;
}
.v-alert {
  margin-bottom: 0 !important;
}
</style>
